body {
  background-color: #d1b884;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

header {
  text-align: center;
  color: #000;
  background-color: #0000;
  width: 100%;
  padding: 100px 0;
  font-family: Arial, serif;
  font-size: 4vw;
  position: fixed;
  top: 0;
}

.container {
  text-align: center;
  border-radius: 10px;
  width: 60vw;
  margin-top: 10vh;
  padding: 4vw;
}

.time {
  color: #000;
  margin-bottom: 0;
  font-family: Arial, sans-serif;
  font-size: 18vw;
  font-weight: bold;
}

.unit {
  color: #333;
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 6vw;
}

.radio-input {
  align-items: center;
  display: flex;
}

.glass {
  z-index: 2;
  backdrop-filter: blur(4px);
  background-color: #75616f80;
  border-radius: 15px;
  width: 30px;
  height: 150px;
  margin-bottom: 0;
  margin-right: 5px;
  padding: 3px;
  box-shadow: 0 10px 20px -5px #32325d33, 0 5px 10px -5px #00000040, inset 0 -1px 8px #0a254042;
}

.glass-inner {
  border: 3px solid #75616f80;
  border-radius: 12px;
  width: 80%;
  height: 96%;
}

.selector {
  flex-direction: column;
  display: flex;
}

.choice {
  align-items: center;
  margin: 5px 0;
  display: flex;
}

.choice > div {
  z-index: 0;
  width: 20px;
  height: 20px;
  margin-right: 5px;
  position: relative;
}

.choice-circle {
  appearance: none;
  cursor: pointer;
  border: 3px solid #75616f80;
  border-radius: 100%;
  width: 100%;
  height: 100%;
  margin: 0;
  box-shadow: 0 0 5px -4px #d368c1, inset 0 0 5px -5px #d368c1;
}

.ball {
  z-index: 1;
  background-color: #3d323c;
  border-radius: 100%;
  transition: transform .4s cubic-bezier(1, -.4, 0, 1.4);
  position: absolute;
  inset: 0;
  transform: translateX(-30px);
  box-shadow: inset 0 -3px 3px #0000002b, inset 0 -5px 5px #00000026, inset 0 -10px 5px #0000001a, 0 .5px .2px #0000000f, 0 1px .5px #00000017, 0 2px 1px #00000017, 0 4px 2px #00000017, 0 8px 4px #00000017, 0 -1px 4px -3px #00000017;
}

.choice-circle:checked + .ball {
  transform: translateX(0);
}

.choice-name {
  color: #313131;
  cursor: pointer;
  font-family: monospace;
  font-size: 14px;
  font-weight: 600;
}
